@font-face {
  font-family: 'dreamvolt';
  src:
    url('asset/font/dreamvolt.ttf?qaiv9k') format('truetype'),
    url('asset/font/dreamvolt.woff?qaiv9k') format('woff'),
    url('asset/font/dreamvolt.svg?qaiv9k#dreamvolt') format('svg');
  font-weight: normal;
  font-style: normal;
  font-display: block;
}

[class^="dreamvolt-"],
[class*=" dreamvolt-"] {
  /* use !important to prevent issues with browser extensions that change fonts */
  font-family: 'dreamvolt' !important;
  speak: never;
  font-style: normal;
  font-weight: normal;
  font-variant: normal;
  text-transform: none;
  line-height: 1;

  /* Better Font Rendering =========== */
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

